import React from "react";
import { StaticImage } from "gatsby-plugin-image"
import { useEffect, useState } from "react";

class Slider extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      prevDisable: true,
      nextDisable: this.refs && this.refs.offsetWidth >= this.refs.scrollWidth ? true : false
    };
  }
  componentDidMount() {
    this.checkButtons(this.refs.offsetWidth, this.refs.scrollWidth);
  }
  checkButtons = (offsetWidthValue, scrollWidthValue) => {
    this.setState({
      prevDisable: this.refs.scrollLeft <= 0 ? true : false,
      nextDisable:
        this.refs.scrollLeft + offsetWidthValue >= scrollWidthValue ? true : false
    });
  };

  render() {
    let widthBlockScroll;
    if (typeof window !== `undefined`) { widthBlockScroll = (window.innerWidth - 100); }
    return (
      <div
        className="slider-container"
        ref={el => {
          this.refs = el;
        }}
      >
        <div className="slider-wrapper">
          {this.props.children
          }
        </div>
        <div
          className={`btn prev `}
          onClick={() => {
            this.refs.scrollLeft -= widthBlockScroll;
            if (this.refs.scrollLeft > 3720) {
              this.refs.scrollLeft = 0
            }
          }}
        >
          {"<"}
        </div>
        <div
          className={`btn next `}
          onClick={() => {
            this.refs.scrollLeft += widthBlockScroll;
            if (this.refs.scrollLeft > 3720) {
              this.refs.scrollLeft = 0
            }
          }}
        >
          {">"}
        </div>
      </div>
    );
  }
}

